.wp-block-create-block-author-block {
	border: 1px dotted #f00;
}

.author-customisation-block {
    /* Styles for the block container */
	padding: 20px;
	background-color: #f9f9f9;
	border: 1px solid #ddd;
	border-radius: 10px;
	box-shadow: #f00;
	max-width: 600px;
	margin: 0 auto;
	text-align: center;
}

.author-customisation-block img {
	border-radius: 50px;
	border: 5px solid grey;
	max-width: 100%; 
	height: auto;
	margin-bottom: 10px;
}

.author-customisation-block p {
	font-size: 16px;
	color: #333
}

